// --------------- type model interface 抽象的名詞

// eslint-disable-next-line max-classes-per-file
export interface EventInfoModel {
  eventYear: string;
  eventName: string;
  eventStartDate: string;
  eventEndDate: string;
  registrationStartDate: string;
  registrationEndDate: string;
  longevityOpen: string;
  prayForOpen: string;
  ceremonyOpen: string;
  eventStatus: boolean;
}

export interface EventMealModel {
  eventDate: string;
  hasMealNumber: number;
  hasBreakfast: number;
  hasLunch: number;
  hasDinner: number;
}

// --------------- class object 實體化的名詞 會動的

export class EventInfo implements EventInfoModel {
  eventYear: string;

  eventName: string;

  eventStartDate: string;

  eventEndDate: string;

  registrationStartDate: string;

  registrationEndDate: string;

  longevityOpen: string;

  prayForOpen: string;

  ceremonyOpen: string;

  eventStatus: boolean;

  constructor(eventYear: string,
    eventName: string,
    eventStartDate: string,
    eventEndDate: string,
    registrationStartDate: string,
    registrationEndDate: string,
    longevityOpen: string,
    prayForOpen: string,
    ceremonyOpen: string,
    eventStatus: boolean) {
    this.eventYear = eventYear;
    this.eventName = eventName;
    this.eventStartDate = eventStartDate;
    this.eventEndDate = eventEndDate;
    this.registrationStartDate = registrationStartDate;
    this.registrationEndDate = registrationEndDate;
    this.longevityOpen = longevityOpen;
    this.prayForOpen = prayForOpen;
    this.ceremonyOpen = ceremonyOpen;
    this.eventStatus = eventStatus;
  }
}

export class EventMeal implements EventMealModel {
  eventDate: string;

  hasMealNumber: number;

  hasBreakfast: number;

  hasLunch: number;

  hasDinner: number;

  constructor(
    eventDate: string,
    hasMealNumber: number,
    hasBreakfast: number,
    hasLunch: number,
    hasDinner: number,
  ) {
    this.eventDate = eventDate;
    this.hasMealNumber = hasMealNumber;
    this.hasBreakfast = hasBreakfast;
    this.hasLunch = hasLunch;
    this.hasDinner = hasDinner;
  }
}
