




























































































































import { FormModel } from '@/models/FormModel';
import Utils from '@/libs/Utils';
import { AttendInfoModel, CeremonyFormModel } from '@/models/CeremonyFormModel';
import { parse } from 'json2csv';
import Vue from 'vue';
import { EventInfoModel, EventMeal } from '@/models/EventFormModel';

export default Vue.extend({
  components: {
  },
  data() {
    return {
      eventDates: [] as any,
      aboutEvent: [] as EventMeal[],
      checkMember: [],
      searchString: '',
      genderFilter: [],
      ageFilter: [],
      noteFilter: [],
      stayFilter: [],
    };
  },
  computed: {
    eventInfo(): EventInfoModel {
      return this.$store.state.eventInfo;
    },
    membersList(): FormModel[] {
      return this.$store.state.membersList;
    },
    attendeesList(): AttendInfoModel[] {
      const vuexlist = this.$store.state.attendeesList;
      const array = [] as AttendInfoModel[];
      vuexlist.forEach((attendInfo: AttendInfoModel) => {
        if (attendInfo.name === ''
          && attendInfo.gender === ''
          && attendInfo.age === ''
          && attendInfo.attendDate === ''
          && attendInfo.attendTime === ''
          && attendInfo.leaveDate === ''
          && attendInfo.leaveTime === ''
          && attendInfo.note === '') {
          // console.log('沒參加');
        } else {
          array.push(attendInfo);
        }
      });
      return array;
    },
    trimSearchString(): string { // 修掉輸入的空白
      const name = this.searchString.trim();
      if (!name) {
        return name;
      }
      return name;
    },
    searchStringList(): AttendInfoModel[] {
      const searchResult = [] as AttendInfoModel[];
      this.attendeesList.forEach((groupsObject: AttendInfoModel) => {
        let groupsName = '';
        groupsName = groupsObject.name;
        const inputString = this.trimSearchString;
        const indexOf = groupsName.toUpperCase().indexOf(inputString.toUpperCase());
        if (indexOf >= 0) {
          searchResult.push(groupsObject);
        }
      });
      return searchResult;
    },
    genderFilterList(): AttendInfoModel[] {
      const filteredResult = [] as AttendInfoModel[];
      this.attendeesList.forEach((groupsObject: AttendInfoModel) => {
        let groupsGender = '';
        groupsGender = groupsObject.gender;
        this.genderFilter.forEach((filterSting) => {
          if (groupsGender === filterSting) {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    ageFilterList(): AttendInfoModel[] {
      const filteredResult = [] as AttendInfoModel[];
      this.attendeesList.forEach((groupsObject: AttendInfoModel) => {
        let groupsAge = '';
        groupsAge = groupsObject.age;
        this.ageFilter.forEach((filterSting) => {
          if (Number(groupsAge) <= 12 && filterSting === 'children') {
            filteredResult.push(groupsObject);
          }
          if (Number(groupsAge) > 12 && Number(groupsAge) < 65 && filterSting === 'adult') {
            filteredResult.push(groupsObject);
          }
          if (Number(groupsAge) >= 65 && filterSting === 'elder') {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    noteFilterList(): AttendInfoModel[] {
      const filteredResult = [] as AttendInfoModel[];
      this.attendeesList.forEach((groupsObject: AttendInfoModel) => {
        let groupsNote = '';
        groupsNote = groupsObject.note;
        this.noteFilter.forEach((filterSting) => {
          if (groupsNote !== '' && filterSting === 'true') {
            filteredResult.push(groupsObject);
          }
          if (groupsNote === '' && filterSting === 'false') {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    stayFilterList(): AttendInfoModel[] {
      const filteredResult = [] as AttendInfoModel[];
      this.attendeesList.forEach((groupsObject: AttendInfoModel) => {
        const groupsAttendDate = groupsObject.attendDate;
        const groupsLeaveDate = groupsObject.leaveDate;
        this.stayFilter.forEach((filterSting) => {
          if (groupsAttendDate === groupsLeaveDate && filterSting === 'true') {
            filteredResult.push(groupsObject);
          }
          if (groupsAttendDate !== groupsLeaveDate && filterSting === 'false') {
            filteredResult.push(groupsObject);
          }
        });
      });
      return filteredResult;
    },
    filteredGroups(): AttendInfoModel[] {
      if (this.trimSearchString !== '') {
        return this.searchStringList;
      }
      if (this.genderFilter.length > 0) {
        return this.genderFilterList;
      }
      if (this.ageFilter.length > 0) {
        return this.ageFilterList;
      }
      if (this.noteFilter.length > 0) {
        return this.noteFilterList;
      }
      if (this.stayFilter.length > 0) {
        return this.stayFilterList;
      }
      return this.attendeesList; // 如果他是0??
    },
  },
  mounted() {
    this.calculateEventDate();
    this.addEventMealTable();
    this.calculateMealNumber();
  },
  methods: {
    addEventMealTable() {
      for (let index = 0; index < this.eventDates.length; index += 1) {
        this.aboutEvent.push(new EventMeal(this.eventDates[index], 0, 0, 0, 0));
      }
    },
    calculateEventDate() {
      const dateArray = [];
      const startDate = Date.parse(this.eventInfo.eventStartDate);
      const endDate = Date.parse(this.eventInfo.eventEndDate);
      const days = Math.ceil((endDate - startDate) / 86400000);
      dateArray.push(this.submitTime(startDate));

      for (let index = 1; index <= days; index += 1) {
        const datetime = startDate + (86400000 * index);
        dateArray.push(this.submitTime(datetime));
      }
      this.eventDates = dateArray;
    },
    calculateMealNumber() {
      this.attendeesList.forEach((attendInfo: AttendInfoModel) => {
        const dateArray = [];
        const attendStart = Date.parse(attendInfo.attendDate);
        const attendEnd = Date.parse(attendInfo.leaveDate);
        const liveDays = Math.ceil((attendEnd - attendStart) / 86400000);
        dateArray.push(this.submitTime(attendStart));

        for (let index = 1; index <= liveDays; index += 1) {
          const datetime = attendStart + (86400000 * index);
          dateArray.push(this.submitTime(datetime));
        }
        const attendDate = dateArray[0];
        const leaveDate = dateArray[dateArray.length - 1];
        const stayAllDayArray = dateArray.slice(1, dateArray.length - 1);

        this.aboutEvent.forEach((thisEvent) => {
          if (attendDate === thisEvent.eventDate) { // 抵達的那天
            if (attendInfo.attendTime === 'beforeBreakfast') { // 早餐前來提供 早餐 午餐 晚餐
              thisEvent.hasBreakfast += 1;
              thisEvent.hasLunch += 1;
              thisEvent.hasDinner += 1;
              thisEvent.hasMealNumber += 1;
            }
            if (attendInfo.attendTime === 'beforeLunch') { // 午餐前來提供 午餐 晚餐
              thisEvent.hasLunch += 1;
              thisEvent.hasDinner += 1;
              thisEvent.hasMealNumber += 1;
            }
            if (attendInfo.attendTime === 'beforeDinner') { // 晚餐前來提供 晚餐
              thisEvent.hasDinner += 1;
              thisEvent.hasMealNumber += 1;
            }
            if (attendInfo.attendTime === 'afterDinner') { // 晚餐後來 不供餐
              // 不供餐
            }
          }
          for (let index = 0; index < stayAllDayArray.length; index += 1) {
            if (stayAllDayArray[index] === thisEvent.eventDate) { // 整天都在的那天
              thisEvent.hasMealNumber += 1; // 人數加一
              thisEvent.hasBreakfast += 1; // 提供三餐
              thisEvent.hasLunch += 1;
              thisEvent.hasDinner += 1;
            }
          }
          if (leaveDate === thisEvent.eventDate) { // 離開的那天
            if (attendDate !== leaveDate) { // 如果抵達日和離開日不同天,
              if (attendInfo.leaveTime === 'beforeBreakfast') { // 早餐前離開 不供餐
              // 不供餐
              }
              if (attendInfo.leaveTime === 'beforeLunch') { // 午餐前離開提供 早餐
                thisEvent.hasBreakfast += 1;
                thisEvent.hasMealNumber += 1;
              }
              if (attendInfo.leaveTime === 'beforeDinner') { // 晚餐前離開提供 早餐 午餐
                thisEvent.hasBreakfast += 1;
                thisEvent.hasLunch += 1;
                thisEvent.hasMealNumber += 1;
              }
              if (attendInfo.leaveTime === 'afterDinner') { // 晚餐後離開提供 早餐 午餐 晚餐
                thisEvent.hasBreakfast += 1;
                thisEvent.hasLunch += 1;
                thisEvent.hasDinner += 1;
                thisEvent.hasMealNumber += 1;
              }
            }
            if (attendDate === leaveDate) { // 如果抵達日和離開日同一天,
              if (attendInfo.leaveTime === 'beforeBreakfast') { // 早餐前離開 取消供早餐
                thisEvent.hasBreakfast -= 1;
              }
              if (attendInfo.leaveTime === 'beforeLunch') { // 午餐前離開 取消供午餐
                thisEvent.hasLunch -= 1;
              }
              if (attendInfo.leaveTime === 'beforeDinner') { // 晚餐前離開 取消供晚餐
                thisEvent.hasDinner -= 1;
              }
              if (attendInfo.leaveTime === 'afterDinner') { // 晚餐後離開 不取消任何設定
                // 不改變
              }
            }
          }
        });
      });
    },
    submitTime(time: number) {
      const date = new Date(time);
      return date.toLocaleDateString();
    },
    viewMemberInfo(attendees: AttendInfoModel) {
      let memberInfo = {} as CeremonyFormModel;
      this.membersList.forEach((memberForm) => {
        memberForm.ceremonyForm.attendInfoList.forEach((attendInfo) => {
          if (attendInfo.name === attendees.name && attendInfo.age === attendees.age) { // 兩項檢測
            memberInfo = memberForm.ceremonyForm;
          }
        });
      });
      this.$router.push('viewMemberInfo');
      this.$store.commit('updateMemberInfo', memberInfo);
    },
    exportCsv() {
      const data = this.filteredGroups.map((attendInfo) => ({
        姓名: attendInfo.name,
        性別: attendInfo.gender,
        年齡: attendInfo.age,
        來寺日期: attendInfo.attendDate,
        離寺日期: attendInfo.leaveDate,
        備註: attendInfo.note,
        填表日期: attendInfo.timestamp,
      }));

      const fields = ['姓名', '性別', '年齡', '來寺日期', '離寺日期', '備註', '填表日期'];
      const opts = { fields };

      try {
        const csv = parse(data, opts);
        console.log(csv);
        this.$notify({
          type: 'success',
          title: '已匯出法會參加者列表',
        });
        Utils.exportCsv('法會參加者列表.csv', csv);
      } catch (err) {
        console.error(err);
      }
    },
  },
});
